import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Helmet } from 'react-helmet';
import { useTranslation } from 'react-i18next';
import {backendUrl} from '../../config/config';

import HeadingBlock from "../../components/HeadingBlock";
import './index.scss';

import Spinner from "../../components/Spinner";
import ErrorIndicator from "../../components/Errors/ErrorIndicator";

const Shares = React.memo((props) => {

    const {t} = useTranslation();

    const [ loading, setLoading ] = useState(true);
    const [ error, setError ] = useState(false);
    const [ shares, setShares ] = useState([]);

    useEffect(() => {
        axios.get(backendUrl + '/actions/')
        .then(res => {
            setShares(res.data);
            setError(false);
        })
        .catch(err => {
            setError(true);
        })
        .finally(() => setLoading(false));
        ;
    }, []);

    const createJSONLD = () => {
        const data = {
            '@context': 'https://schema.org/',
            "@type": "FoodEvent",
            'organizer': {
                '@type': 'Person',
                'name': '24ROLLS'
            },
            'performer': {
                '@type': 'Person',
                'name': '24ROLLS'
            },
            'attendee': {
                '@type': 'Person',
                'name': '24ROLLS'
            },
            'typicalAgeRange': '0-100',
            'about': shares.map(el => el.title).join(', '),
            'description': shares.map(el => el.description).join(', '),
            'location': 'Днепр, Запорожье',
            'name': 'Акции',
            'startDate': new Date().getFullYear(),
        };

        return JSON.stringify(data);
    }

    return (
        <div style={{display: 'flex', flexDirection: 'column', alignItems: 'center'}}>

            <Helmet>
            <title>Суши 24 Доставка Днепр, Запорожье | Заказать еду на дом или офис | Меню и цены на суши, роллы, сеты. Официальный онлайн сайт Доставки еды</title>
                <meta
                    name="description"
                    content="Свежие акционные предложения, акции на самые лучшие товары: суши, роллы, wok,паста, онлайн заказ новых сетов на дом и офис!"
                />
                <meta
                    name="keywords"
                    content="Заказать суши, роллы, wok, пасту, салаты, супы, напитки, доставка, Днепр, Запорожье, Харьков, вкусно, быстрая доставка, акции, скидки, выгодные предложения, подарки, заказать Суши Днепр, заказать Суши Запорожье, заказать Суши Харьков, круглосуточная доставка суши, 24ROLLS, rolls, 24rolls.com.ua"
                />
                <meta property="twitter:title" content="ROLLS24" />
                <meta property="og:title" content="ROLLS24" />
                <meta
                    name="twitter:description"
                    content="Суши Доставка. Заказывай еду с бесплатной доставкой в Днепре и Запорожье✔️Суши от 15грн ☎ 050 719-24-24 ✔️Роллы ✔️Сеты ✔️Wok ✔Онлайн заказ еды на дом,офис."
                />
                <meta
                    name="og:description"
                    content="Суши Доставка. Заказывай еду с бесплатной доставкой в Днепре и Запорожье✔️Суши от 15грн ☎ 050 719-24-24 ✔️Роллы ✔️Сеты ✔️Wok ✔Онлайн заказ еды на дом,офис."
                />
                <script async type="application/ld+json">
                    {createJSONLD()}
                </script>
            </Helmet>

            <HeadingBlock title={t('shares.title')} svg={true} h={'h1'} />
             {loading && <Spinner /> }
             {error && <ErrorIndicator />}
            {shares[0] &&
                <div className="masonry-layout">
                    {
                        shares.map(action =>
                            <div className="masonry-layout__panel" key={action._id}>
                                <div className="masonry-layout__panel__header">
                                    <img src={backendUrl + `/${action.image}`} alt="rolls"/>
                                </div>
                                <div className="masonry-layout__panel__content">
                                    <h4>{action.title}</h4>
                                    <p>{action.description}</p>
                                </div>
                            </div>
                        )
                    }
                </div>
            }
            {
                !shares[0] && !loading && !error &&
                <p>{'Пока что акций нет'}</p>
            }
        </div>
    );
});

export default Shares;